import React from 'react';
import { graphql, HeadFC, PageProps, Link } from 'gatsby';
import Layout from '../components/Layout/Layout';
import LabCard from '../components/LabCard/LabCard';
import Button from '../components/Button/Button';

interface CategoryListProps extends PageProps {
  data: {
    allMdx: {
      nodes: {
        id: string;
        frontmatter: {
          title: string;
          slug: string;
          level: string;
          duration: string;
          topics: number;
        };
        excerpt: string;
        parent?: {
          relativeDirectory: string;
        };
      }[];
    };
  };
  pageContext: {
    category: string;
  };
}

const CategoryList: React.FC<CategoryListProps> = ({ data, pageContext }) => {
  // Strict filter to ensure we get only this category's files
  const labs = data.allMdx.nodes.filter(
    (node) => node.parent?.relativeDirectory === pageContext.category,
  );
  const categoryTitle =
    pageContext.category.charAt(0).toUpperCase() +
    pageContext.category.slice(1);

  // Simple category mapping for descriptions/icons (could be moved to a config)
  const categoryInfo: Record<string, { description: string; icon: string }> = {
    etymology: {
      description: `Explore the history and origin of words.`,
      icon: `üìö`,
    },
    phonetics: { description: `Master the sounds of English.`, icon: `üîä` },
    patterns: {
      description: `Unlock the secrets of spelling rules.`,
      icon: `‚úçÔ∏è`,
    },
    logic: {
      description: `Puzzles to test your linguistic reasoning.`,
      icon: `üß©`,
    },
  };

  const info = categoryInfo[pageContext.category] || {
    description: `Interactive exercises.`,
    icon: `üìñ`,
  };

  return (
    <Layout>
      <div style={{ marginBottom: `2rem` }}>
        <Link to="/labs" style={{ textDecoration: `none` }}>
          <Button variant="secondary" size="small">
            ‚Üê Back to Word Labs
          </Button>
        </Link>
      </div>

      <div style={{ textAlign: `center`, marginBottom: `4rem` }}>
        <h1 style={{ fontSize: `3rem`, marginBottom: `1rem` }}>
          {info.icon} {categoryTitle}
        </h1>
        <p
          style={{
            fontSize: `1.25rem`,
            color: `var(--color-text-secondary)`,
            maxWidth: `600px`,
            margin: `0 auto`,
          }}
        >
          {info.description}
        </p>
      </div>

      <div
        style={{
          display: `grid`,
          gap: `2rem`,
          gridTemplateColumns: `repeat(auto-fit, minmax(340px, 1fr))`,
        }}
      >
        {labs.map((lab) => (
          <LabCard
            key={lab.id}
            title={lab.frontmatter.title}
            description={lab.excerpt}
            // IMPORTANT: Link to nested path
            slug={`${pageContext.category}/${lab.frontmatter.slug}`}
            level={lab.frontmatter.level || `Beginner`}
            duration={lab.frontmatter.duration || `15 min`}
            topics={lab.frontmatter.topics || 3}
            icon={info.icon} // Reuse category icon for now
          />
        ))}
      </div>
    </Layout>
  );
};

export default CategoryList;

export const Head: HeadFC<
  CategoryListProps['data'],
  CategoryListProps['pageContext']
> = ({ pageContext }) => (
  <title>
    {pageContext.category.charAt(0).toUpperCase() +
      pageContext.category.slice(1)}
    {` `}| Word Labs
  </title>
);

export const query = graphql`
  query {
    allMdx(sort: { frontmatter: { date: DESC } }) {
      nodes {
        id
        excerpt(pruneLength: 80)
        frontmatter {
          title
          slug
          level
          duration
          topics
        }
        parent {
          ... on File {
            relativeDirectory
          }
        }
      }
    }
  }
`;
